@import "../../styles/mixins";

.modalDialog {
  height: 35rem;

  @supports (height: env(safe-area-inset-bottom)) {
    height: calc(35rem + env(safe-area-inset-bottom));
  }
}

.slide {
  padding-top: 0 !important;
}

.container {
  display: flex;
  flex-direction: column;

  height: 100%;
  padding: 0 1rem 1rem;
}

.content {
  display: flex;
  flex-direction: column;

  padding: 0 1rem 1rem;
}

.iconBlock {
  position: relative;

  display: flex;
  justify-content: center;

  height: 10rem;
  margin: 0 -1rem 1.25rem;

  &::after {
    content: "";

    display: block;

    width: 100%;
    max-width: 23.4375rem;

    background-clip: padding-box, padding-box;
    background-origin: border-box;
    background-size: cover, cover;
  }

  &_base {
    &::after {
      background-image: url(../../assets/ledger/laptop-base.png);

      :global(.theme-dark) & {
        background-image: url(../../assets/ledger/laptop-base-dark.png);
      }
    }
  }

  &_success {
    &::after {
      background-image: url(../../assets/ledger/laptop-success.png);

      :global(.theme-dark) & {
        background-image: url(../../assets/ledger/laptop-success-dark.png);
      }
    }
  }
}

.textBlock {
  display: flex;
  flex-direction: column;

  padding: 0.75rem;

  background: var(--color-gray-button-background-light);
  border-radius: var(--border-radius-default);

  &_gap {
    gap: 0.5rem;
  }

  &_success {
    color: var(--color-transaction-green-text);

    background: var(--color-transaction-green-background);
  }

  &_small {
    padding: 0.75rem;
  }
}

.textIcon {
  display: flex;
  align-self: flex-start;

  font-size: 1.25rem;
  line-height: 1;
}

.text {
  display: flex;
  gap: 0.25rem;
  align-items: center;

  font-size: 0.9375rem;
  font-weight: 500;
  color: var(--color-gray-1);

  &_failed {
    color: var(--color-red);
  }

  &_connected {
    color: var(--color-blue);
  }

  &_success {
    color: var(--color-transaction-green-text);
  }
}

.actionBlock {
  display: flex;
  gap: 1rem;

  margin-top: auto;

  &_single {
    justify-content: center;
  }
}

.button {
  width: 100%;

  &_single {
    width: unset;
  }
}

.accounts {
  overflow-y: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 0.5rem;

  padding: 0.5rem;

  @include adapt-padding-to-scrollbar(0.5rem); /* stylelint-disable-line order/order */

  background-color: var(--color-background-first);
  border-radius: var(--border-radius-default);
}

.account {
  cursor: var(--custom-cursor, pointer);

  position: relative;

  overflow: hidden;
  display: flex;
  flex-direction: column;

  width: 100%;
  height: 3.75rem;
  margin: 0;
  padding: 0 0.5rem;

  font-size: 0.75rem;
  font-weight: 600;
  color: var(--color-card-text);
  text-align: left;

  opacity: 0.84;
  background-image: url('../../assets/account_button_bg.jpg'), linear-gradient(125deg, #71A9ED 0, #436CB6 100%);
  background-clip: padding-box, padding-box;
  background-origin: border-box;
  background-size: cover, cover;
  border: none;
  border-radius: var(--border-radius-tiny);

  transition: opacity 200ms;

  &_current {
    cursor: default;

    opacity: 1;
  }

  @media (min-resolution: 1.5dppx) {
    background-image: url('../../assets/account_button_bg@2x.jpg'), linear-gradient(125deg, #71A9ED 0, #436CB6 100%);
  }
}

.accountName {
  overflow: hidden;

  max-width: 100%;
  margin-top: 0.5rem;
  padding-right: 1.125rem;

  text-overflow: ellipsis;
  white-space: nowrap;

  @supports (-webkit-line-clamp: 2) {
    display: -webkit-box;

    line-height: 0.8125rem;
    white-space: normal;

    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
}

.accountFooter {
  display: flex;
  align-items: center;

  margin-top: auto;
  margin-bottom: 0.5rem;

  font-size: 0.6875rem;
  line-height: 0.6875rem;
}

.accountCurrencyIcon {
  margin-right: 0.125rem;

  font-size: 0.5rem;
  line-height: 0.5rem;
}

.accountAddress {
  color: var(--color-card-second-text);
}

.accountCheckMark {
  position: absolute;
  top: 0.3125rem;
  right: 0.3125rem;

  width: 1rem;
  height: 1rem;

  border-radius: var(--border-radius-default);

  /* stylelint-disable-next-line plugin/whole-pixel */
  box-shadow: inset 0 0 0 0.075rem var(--color-card-toggle);

  &_active {
    background: var(--color-blue) url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSI4IiBoZWlnaHQ9IjciIGZpbGw9Im5vbmUiPjxwYXRoIHN0cm9rZT0iI2ZmZiIgc3Ryb2tlLWxpbmVjYXA9InJvdW5kIiBzdHJva2UtbGluZWpvaW49InJvdW5kIiBzdHJva2Utd2lkdGg9IjEuNCIgZD0ibTEgMy41IDIgMkw3IDEiLz48L3N2Zz4=") no-repeat 50% 50%;
    /* stylelint-disable-next-line plugin/whole-pixel */
    box-shadow: inset 0 0 0 0.075rem var(--color-white);
  }
}

.sticker {
  margin: 0 auto 1.25rem;
}

.declinedLabel {
  display: flex;
  align-items: center;

  margin: 0.75rem auto 0;
  padding: 0.375rem 0.5rem;

  font-size: 1.0625rem;
  font-weight: 700;
  color: var(--color-transaction-red-text);
  text-align: center;

  background-color: var(--color-transaction-red-background);
  border-radius: var(--border-radius-tiny);
}
